import React, { Component } from 'react';
import './App.css';
import { Motion, spring } from 'react-motion';
import { PageLoader, RevealText, MenuButton, Menu } from './components';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showHeadline: false,
      showAboutMe: false,
      showMenuButton: false,
      showContact: false,
      showSocialMedia: false,
      menuOpened: false
    }

    this.timeline = {
      '0': () => ({ showHeadline: true }),
      '750': () => ({ showMenuButton: true }),
      '1000': () => ({ showAboutMe: true }),
      '1250': () => ({ showContact: true }),
      '1500': () => ({ showSocialMedia: true })
    }
  }

  pageLoaded = () => {
    Object.keys(this.timeline).forEach((time) => {
      const newState = this.timeline[time];
      setTimeout(() => this.setState(newState()), parseInt(time, 10));
    });
  };

  render() {
    const { showHeadline, showAboutMe, showMenuButton, showContact, showSocialMedia, menuOpened } = this.state;

    return (
      <div>
        <div className="page page1">
          <PageLoader done={this.pageLoaded} />
          <Menu open={menuOpened} />
          { showMenuButton && <MenuButton open={menuOpened} onClick={() => this.setState({ menuOpened: !menuOpened })} /> }
          { showHeadline && <RevealText text={'Chu Hoang Son'} /> }
          { showAboutMe && <Motion defaultStyle={{ opacity: 0, y: 20 }}
            style={{ opacity: spring(1), y: spring(0) }}>
            {({ opacity, y }) =>
              <p style={{ opacity, transform: `translateY(${y}px)` }}><span className="bigger">I’m a front-end developer</span>, living in Espoo, Finland. I’m passionate about design, animation, and care lots about user experience. If you serve a hamburger, you don’t just give your customer a bun, meat and salad, you stack them into a neat and nice deliceous hamburger. And yes, I just made that up.</p>
            }
          </Motion> }
          { showContact && <Motion defaultStyle={{ opacity: 0, y: 20 }}
            style={{ opacity: spring(1), y: spring(0) }}>
            {({ opacity, y }) =>
              <p style={{ opacity, transform: `translateY(${y}px)` }}>
                <a href="mailto:chuson1996@gmail.com" target="_blank">chuson1996@gmail.com</a> <br/>
                Available for freelance projects
              </p>
            }
          </Motion> }
          { showSocialMedia && <Motion defaultStyle={{ opacity: 0, y: 20 }}
            style={{ opacity: spring(1), y: spring(0) }}>
            {({ opacity, y }) =>
              <p style={{ opacity, transform: `translateY(${y}px)` }}>
                <a href="#" target="_blank">LinkedIn</a> <br/>
                <a href="https://www.instagram.com/code_everyday/" target="_blank">Instagram</a>
              </p>
            }
          </Motion> }
        </div>
      </div>
    );
  }
}

export default App;



